import app from '@/common/app.js';
import loadcache from '@/common/loadcache.js';
import request from '@/common/request.js';
export default {
    data() {
        return {
            typeArr: ['请选择'],
            typeIndex: 0,
            type: '',
            contact: '',
            id: '',
            hasDelete: false,
            saveFlag: false,
            deleteFlag: false
        }
    },
    onLoad(event) {
        this.typeArr = loadcache.getDictsByTypeCode(app.DICT_TYPECODE.CONTACT_TYPE);
        const payload = event.id || event.payload;
        if (payload != null) {
            try {
                this.id = JSON.parse(decodeURIComponent(payload));
            } catch (error) {
                this.id = JSON.parse(payload);
            };

            this.hasDelete = true;
        }

    },
    onShow(e) {
        if (this.id != '') {
            this.getContact();
        }
    },
    methods: {
        getContact(e) {
            request.http({
                url: '/app/contact/getOne',
                header: 'application/x-www-form-urlencoded',
                method: "POST",
                data: {
                    id: this.id
                },
                success: (res) => {
                    if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                        this.type = res.data.data.type;
                        this.contact = res.data.data.contact;
                        this.typeArr.forEach((d, index) => {
                            if (d.value == this.type) {
                                this.typeIndex = index;
                            }
                        });

                    }
                },
                error: (res) => {}
            });
        },
        bindPickerChangeType(e) {
            this.typeIndex = e.target.value;
        },
        saveContact(e) {
            this.saveFlag = true;
            this.type = this.typeArr[this.typeIndex].value;
            if (this.contact == '') {
                this.saveFlag =false;
                this.showpageToast("请输入联系内容");
                return false;
            }
            if (this.type == 'PHONE') {
                
                let myphone =  /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1})|(19[0-9]{1})|(17[0-9]{1}))+\d{8})$/;
                if (!myphone.test(this.contact)) {
                    this.saveFlag =false;
                    this.showpageToast("请输入正确的手机号");
                    return false;
                }
            }
            if (this.type == 'EMAIL') {
                this.saveFlag =false;
                let myemail = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
                if (!myemail.test(this.contact)) {
                    this.saveFlag =false;
                    this.showpageToast("请输入正确的邮箱");
                    return false;
                }
            }
            if (this.type == 'QQ') {
                this.saveFlag =false;
                let myqq = /^[1-9][0-9]{4,14}/;
                if (!myqq.test(this.contact)) {
                    this.saveFlag =false;
                    this.showpageToast("请输入正确的QQ");
                    return false;
                }
            }
            request.http({
                url: '/app/contact/saveContacat',
                header: 'application/x-www-form-urlencoded',
                method: "POST",
                data: {
                    id: this.id,
                    type: this.type,
                    contact: this.contact
                },
                success: (res) => {
                    if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                        uni.navigateBack({

                        });
                        this.showpageToast(res.data.msg);
                    }
                    this.saveFlag = false;
                },
                error: (res) => {
                    this.saveFlag = false;
                }
            });
        },
        deleteContact(e) {
            this.deleteFlag = true;
            uni.showModal({
                title: '',
                content: '确定删除该联系方式？',
                success: (res) => {
                    if (res.confirm) {
                        request.http({
                            url: '/app/contact/deleteContact',
                            header: 'application/x-www-form-urlencoded',
                            method: "POST",
                            data: {
                                id: this.id
                            },
                            success: (res) => {
                                if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                                    uni.navigateBack({

                                    });
                                    this.showpageToast(res.data.msg);
                                }
                                this.deleteFlag =false;
                            },
                            error: (res) => {
                                this.deleteFlag =false;
                            }
                        });


                    }else{
                        this.deleteFlag =false;
                    }
                }
            });
        },
        showpageToast(msg) {
            uni.showToast({
                icon: 'none',
                title: msg
            });
        },
    }
}
